<template>
  <div class="content">
<!--     下拉框-->
    <Select1></Select1>
<!--   统计表-->
    <div class="statis  statisTop">
      <div class="statiStical">
        <h2><span></span><p>收益数据</p></h2>
        <ul>
          <li>
            <div>
              <span>收益总计</span>
              <p>40,7925</p>
            </div>
          </li>
          <li>
            <span>收益总计</span>
            <p>40,7925</p>
          </li>
          <li>
            <span>收益总计</span>
            <p>40,7925</p>
          </li>
        </ul>
      </div>
      <div class="marginTop">
<!--        <eCharts></eCharts>-->
        <div id="eCharId1" class="eChars"></div>
        <div class="marginTop">
          <timeList  @timeChang="timeChang"></timeList>
        </div>
      </div>
    </div>
    <div class="statis WithdrawalData">
      <div class="statiStical">
        <h2><span></span><p>提现数据</p></h2>
        <ul>
          <li>
            <div>
              <span>提现总计</span>
              <p>{{AmountData.cashAmount}}</p>
            </div>
          </li>
          <li>
            <span>单日最高提现</span>
            <p>{{AmountData.theHighestCash}}</p>
          </li>
          <li>
            <span>日均提现</span>
            <p>{{AmountData.averageDaily}}</p>
          </li>
        </ul>
      </div>
      <div class="marginTop">
        <div id="eCharId2" class="eChars"></div>
        <div class="marginTop">
          <timeList  @timeChang="timeChang2"></timeList>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  const timeList = () => import("../../../components/timeList2.vue");
  const Select1 = () => import("../../../components/Select.vue");
  export default {
    components:{
      timeList,
      Select1
    },
    data() {
      return {
        day:this.common.timeForMat(7,true)+" - "+this.common.timeForMat(0,true),
        itemId:"-2",
        pathData:{
          beginDate:this.common.timeForMat(7,false),
          endDate:this.common.timeForMat(0,false),
          storeId:"",
        },
        pathData2:{
          minTime:this.common.timeForMat(7),
          maxTime:this.common.timeForMat(0),
        },
        AmountData:{
          averageDaily:"",
          cashAmount:"",
          theHighestCash:"",
        }
      }
    },
    computed:{
       changSelrct() {
           return this.$store.state.shopId
       }
    },
    watch:{
      changSelrct(val){
         this.pathData.storeId=val;
        // this.storeAmount();
        this.common.eChart("eCharId1");
      }
  },
    mounted(){
      this.overviewData();
    },
    methods:{
      storeAmount(){
        this.http.get3("csh_m/statCenter/storeAmount/v1",this.pathData,this.$store.state.token)
          .then(res => {
            console.log(res)
            console.log(res.data.resultCode)
            if(res.data.resultCode==1){

            }else{
              this.selectList=[];
              Dialog.alert({
                message:"获取门店数据失败！"
              }).then(() => {
              });
            }
          })
      },
      overviewData(){
         var that=this;
        this.http.get2("cashrecord/overviewData",this.pathData2,this.$store.state.token)
          .then(res => {
            console.log(res)
            if(res.data.resultCode==1){
              let amountByDayVOS=res.data.data.amountByDayVOS;
              that.AmountData.averageDaily=that.common.Money2(res.data.data.averageDaily);
              that.AmountData.cashAmount=that.common.Money2(res.data.data.cashAmount);
              that.AmountData.theHighestCash=that.common.Money2(res.data.data.theHighestCash);
              console.log(this.AmountData)
              let amountArr=[];
              let timesArr=[];
              amountByDayVOS.forEach(function(item,index) {
                amountArr.push(that.common.Money2(item.amount));
                timesArr.push(item.times);
              })
              this.common.eChart("eCharId2",amountArr,timesArr);
            }else{
              this.selectList=[];
              Dialog.alert({
                message:"获取门店数据失败！"
              }).then(() => {
              });
            }
          })
      },
      timeChang(data){
        if(data.length>10){
          this.day=this.common.transForm(data);

        }else {
          this.day=this.common.transForm(data)+" - "+this.common.timeForMat(0,true);
        }
        this.overviewData();
      },
      timeChang2(data){
        if(data.length>10){
          this.day=this.common.transForm(data);
          this.pathData2={
            minTime:this.common.stateLong(data.split(" - ")[0]),
            maxTime:this.common.stateLong(data.split(" - ")[1])
          }
        }else {
          this.pathData2={
            minTime:this.common.stateLong(data),
            maxTime:this.common.timeForMat(0)
          };
        }
        this.overviewData();
      },
    },
  }
</script>
<style scoped lang="scss">
  .eChars{
    width: 100%;
    height: 472px;
  }
.marginTop{
  margin-top:20px;
}
.statis{
   background: #fff;
   padding-bottom: 42px;

}
.statisTop{
  margin-top:100px;
}
.WithdrawalData{
   border-top: 20px solid #F8F8F8;
  padding-bottom: 150px;
}
.statiStical{
    padding-top: 20px;
}
.statiStical h2{
  height: 60px;
  padding: 10px  36px;
}
.statiStical  h2 p{
  text-align: left;
  float: left;
  height: 40px;
  line-height: 40px;
  font-size:28px;
  font-family:PingFangSC;
  font-weight:500;
  color:rgba(51,51,51,1);
   margin-left: 20px;
}
.statiStical  h2 span{
    height: 28px;
    width: 10px;
    background: #FF8D59;
    float: left;
    margin-top: 6px;
}
.statiStical ul{
   margin-top: 20px;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
}
.statiStical ul li{
   flex: 1;
  padding-left: 30px;
  box-sizing: border-box;
  border-left: 1px solid #E6E8EE;
}
.statiStical ul li:nth-child(1){
  border: none;
}
.statiStical ul li div{
   margin: 0 auto;
  display: inline-block;
}
.statiStical ul li span{
  font-size:24px;
  font-family:PingFangSC;
  font-weight:400;
  color:rgba(153,153,153,1);
  line-height:34px;
  text-align: left;
  display: block;
}
.statiStical ul li p{
  text-align: left;
  font-size:48px;
  font-family:PingFangSC;
  font-weight:600;
  color:rgba(51,51,51,1);
  line-height:76px;
}
  .content{
     min-height: 100%;
  }

</style>



